const art20200616004 = "#  React SPA server React SSR server\n\n **Server Side Rendering** 2:\n* **Html.js**:  `id=\"content\"` Route\n* **server.js**:  `StaticRouter` Route `renderToString` `Html.js`\n\n`Html.js` 2:\n```\nconst {\n  content,\n  app,\n} = this.props;\n\n<div id=\"content\" dangerouslySetInnerHTML={{ __html: content }} />\n```\n`server.js`:\n```\n  const context = {};\n  const component = (\n    <StaticRouter\n      location={req.url}\n      context={context}\n    >\n      <App app={urlConfig} />\n    </StaticRouter>\n  );\n  const content = renderToString(component);\n\n  res.send(`<!doctype html>\\n${renderToStaticMarkup(<Html\n    app={urlConfig}\n    content={content}\n  />)}`);\n```\n\n---\n\n**Html.js**:\n```\nimport React, { Component } from 'react';\n\nclass Html extends Component {\n  render() {\n    const {\n      content,\n      app,\n    } = this.props;\n\n    const {\n      staticUrl,\n      cssUrl,\n    } = app;\n\n    return (\n      <html lang=\"zh-TW\">\n        <head>\n          <meta name=\"viewport\" content=\"width=device-width, initial-scale=1\" />\n          <meta httpEquiv=\"x-ua-compatible\" content=\"IE=edge\" />\n          <link rel=\"stylesheet\" href={`${cssUrl}bootstrap.css`} />\n          <link rel=\"stylesheet\" href={`${cssUrl}index.css`} />\n          <link rel=\"stylesheet\" href={`${cssUrl}App.css`} />\n        </head>\n        <body>\n          <div id=\"content\" dangerouslySetInnerHTML={{ __html: content }} />\n          <script type=\"text/javascript\" src={`${staticUrl}bundle.js`} />\n        </body>\n      </html>\n    );\n  }\n}\n\nexport default Html;\n```\n**server.js**:\n```\nimport express from 'express';\nimport React from 'react';\nimport { renderToStaticMarkup, renderToString } from 'react-dom/server';\nimport { StaticRouter } from 'react-router-dom';\n\nimport Html from './app/containers/Html';\nimport App from './app/containers/App';\n\nconst app = express();\nconst port = 3000;\n\n//  router\napp.use('/static', express.static('public'));\n\n//  Html.js server side\napp.use((req, res) => {\n  const urlConfig = {\n    mainUrl: 'http://localhost:3000/',\n    staticUrl: 'http://localhost:3000/static/',\n    cssUrl: 'http://localhost:3000/static/css/',\n    imageUrl: 'http://localhost:3000/static/image/',\n  };\n\n  const context = {};\n  const component = (\n    <StaticRouter\n      location={req.url}\n      context={context}\n    >\n      <App app={urlConfig} />\n    </StaticRouter>\n  );\n  const content = renderToString(component);\n  res.send(`<!doctype html>\\n${renderToStaticMarkup(<Html\n    app={urlConfig}\n    content={content}\n  />)}`);\n});\n\napp.listen(port, function () {\n  console.log('Express app started on ' + port);\n});\n```\n:\n```\n$ npm run watch\n$ npm run dev\n```\n `http://localhost:3000/`\n\n---\n\n##  Server side rendering?\n1.  chrome devtools **Network** Html\n2.  Network **Html** ** request**\n3. ** url enter** Network Html** Html** Server side rendering!";

