const art20200616002 = "#  Express server React SPA server\n\n **Express server** ReactJS\n\n---\n\n SPA **server side** html **client side** server `bundle.js` `webpack.config.js`:\n* **client**:  `bundle.js``bundle.js` `/` `public`\n* **server**:  `server.js``server.js` `/` `dist`\n\nwebpack.config.js:\n```\nconst path = require('path');\n\nconst client = {\n  devtool: 'source-map',\n  target: 'web',\n  context: path.resolve(__dirname, 'src'),\n  entry: { bundle: './client.js' },\n  output: {\n    publicPath: '/',\n    path: path.resolve(__dirname, 'public'),\n    filename: '[name].js',\n  },\n  module: {\n    rules: [\n      {\n        test: /\\.js?$/,\n        include: path.resolve(__dirname, 'src'),\n        loader: 'babel-loader',\n        query: {\n          plugins: ['transform-decorators-legacy'],\n          presets: ['env', 'react', 'stage-0'],\n        },\n      },\n    ],\n  },\n};\n\nconst server = {\n  devtool: 'source-map',\n  target: 'node',\n  node: {\n    console: false,\n    global: true,\n    process: true,\n    Buffer: true,\n    __filename: 'mock',\n    __dirname: 'mock',\n    setImmediate: true,\n  },\n  context: path.resolve(__dirname, 'src'),\n  entry: { server: './server.js' },\n  output: {\n    path: path.resolve(__dirname, 'dist'),\n    filename: '[name].js',\n  },\n  module: {\n    rules: [\n      {\n        test: /\\.js?$/,\n        include: path.resolve(__dirname, 'src'),\n        loader: 'babel-loader',\n        query: {\n          plugins: ['transform-decorators-legacy'],\n          presets: ['env', 'react', 'stage-0'],\n        },\n      },\n    ],\n\n  },\n};\n\nmodule.exports = [client, server];\n```\n **client** `entry` `key-value` key bundle `output.filename` `[name].js` `bundle.js`\n\n `bundle.js` **client** `target` `web`\n\n---\n\n## `/` `public/` `bundle.js`:\n\n `public/` `css/` `image/`\n\n React `logo.svg` `image/`\n\n `css/` `index.css` `App.css`\n\nindex.css:\n```\nbody {\n  margin: 0;\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',\n    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',\n    sans-serif;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\ncode {\n  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',\n    monospace;\n}\n```\nApp.css:\n```\n.App {\n  text-align: center;\n}\n\n.App-logo {\n  height: 40vmin;\n  pointer-events: none;\n}\n\n@media (prefers-reduced-motion: no-preference) {\n  .App-logo {\n    animation: App-logo-spin infinite 20s linear;\n  }\n}\n\n.App-header {\n  background-color: #282c34;\n  min-height: 100vh;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  font-size: calc(10px + 2vmin);\n  color: white;\n}\n\n.App-link {\n  color: #61dafb;\n}\n\n@keyframes App-logo-spin {\n  from {\n    transform: rotate(0deg);\n  }\n  to {\n    transform: rotate(360deg);\n  }\n}\n```\n![](/static/image/20200611163145.png \"20200611163145\")\n\n---\n\n `react` `react-dom`:\n```\n$ npm install react --save\n$ npm install react-dom --save\n```\n\n---\n\n## `src/` `app/containers/`:\n\n `containers/` `App.js`:\n```\nimport React, { Component } from 'react';\n\nclass App extends Component {\n  constructor(props) {\n    super(props);\n    this.state = {};\n  }\n\n  render() {\n    const {\n      app,\n    } = this.props;\n    const {\n      imageUrl,\n    } = app;\n\n    return (\n      <div className=\"App\">\n        <header className=\"App-header\">\n          <img src={`${imageUrl}logo.svg`} className=\"App-logo\" alt=\"logo\" />\n          <p>\n            Edit <code>src/App.js</code> and save to reload.\n          </p>\n          <a\n            className=\"App-link\"\n            href=\"https://reactjs.org\"\n            target=\"_blank\"\n            rel=\"noopener noreferrer\"\n          >\n            Learn React\n          </a>\n        </header>\n      </div>\n    );\n  }\n}\n\nexport default App;\n```\n\n---\n\n##  `src/` `client.js` `bundle.js`:\n```\nimport React from 'react';\nimport { render } from 'react-dom';\n\nimport App from './app/containers/App';\n\nconst app = {\n  mainUrl: 'http://localhost:3000/',\n  staticUrl: 'http://localhost:3000/static/',\n  cssUrl: 'http://localhost:3000/static/css/',\n  imageUrl: 'http://localhost:3000/static/image/',\n};\n\nconst component = (\n  <App\n    app={app}\n  />\n);\n\nrender(\n  component,\n  document.getElementById('content'),\n);\n```\n##  `containers/` `Html.js` server side html:\n```\nimport React, { Component } from 'react';\n\nclass Html extends Component {\n  render() {\n    const {\n      app,\n    } = this.props;\n\n    const {\n      staticUrl,\n      cssUrl,\n    } = app;\n\n    return (\n      <html lang=\"zh-TW\">\n        <head>\n          <meta name=\"viewport\" content=\"width=device-width, initial-scale=1\" />\n          <meta httpEquiv=\"x-ua-compatible\" content=\"IE=edge\" />\n          <link rel=\"stylesheet\" href={`${cssUrl}bootstrap.css`} />\n          <link rel=\"stylesheet\" href={`${cssUrl}index.css`} />\n          <link rel=\"stylesheet\" href={`${cssUrl}App.css`} />\n        </head>\n        <body>\n          <div id=\"content\" />\n          <script type=\"text/javascript\" src={`${staticUrl}bundle.js`} />\n        </body>\n      </html>\n    );\n  }\n}\n\nexport default Html;\n```\n `bundle.js` `bundle.js` `id=\"content\"`\n\n![](/static/image/20200611173113.png \"20200611173113\")\n\n `server.js` `res.send('Hello World');`:\n```\n//  Html.js server side\napp.use((req, res) => {\n  const urlConfig = {\n    mainUrl: 'http://localhost:3000/',\n    staticUrl: 'http://localhost:3000/static/',\n    cssUrl: 'http://localhost:3000/static/css/',\n    imageUrl: 'http://localhost:3000/static/image/',\n  };\n\n  res.send(`<!doctype html>\\n${renderToStaticMarkup(<Html\n    app={urlConfig}\n  />)}`);\n});\n```\n `server.js`:\n```\nimport express from 'express';\nimport React from 'react';\nimport { renderToStaticMarkup } from 'react-dom/server';\n\nimport Html from './app/containers/Html';\n\nconst app = express();\nconst port = 3000;\n\n//  router\napp.use('/static', express.static('public'));\n\n//  Html.js server side\napp.use((req, res) => {\n  const urlConfig = {\n    mainUrl: 'http://localhost:3000/',\n    staticUrl: 'http://localhost:3000/static/',\n    cssUrl: 'http://localhost:3000/static/css/',\n    imageUrl: 'http://localhost:3000/static/image/',\n  };\n\n  res.send(`<!doctype html>\\n${renderToStaticMarkup(<Html\n    app={urlConfig}\n  />)}`);\n});\n\napp.listen(port, function () {\n  console.log('Express app started on ' + port);\n});\n```\n\n---\n\n:\n```\n$ npm run watch\n$ npm run dev\n```\n `http://localhost:3000/`\n\n##  SPA?\n\n chrome devtools `Sources` tab\n\n `(index)` html (`id=\"content\"`)\n\n![](/static/image/20200611173136.png \"20200611173136\")\n\n `static/bundle.js`!\n\n SPA `react-router` client side";

