import React, { Fragment, useState } from 'react';
import { CSSTransition } from 'react-transition-group'

import Item from './Item';
import XBurger from './XBurger';

const NavBar = (props) => {
  const {
    history,
    currentCode,
  } = props;
  const [isShow, setShow] = useState(false);

  const handleShowChange = (e) => {
    setShow(!isShow);
  }

  return (
    <Fragment>
      <CSSTransition
          in={isShow}   // 
          timeout={1000}           // 
          classNames="navBarShow"   //  namespace
      >
        <div className={`navBar${isShow ? " show" : ""}`}>
          <ul className="form">
            <Item
              code ={"art20200616001"}
              text={" Express server"}
              currentCode={currentCode}
              history={history}
            />
            <Item
              code ={"art20200616002"}
              text={" React SPA server"}
              currentCode={currentCode}
              history={history}
            />
            <Item
              code ={"art20200616003"}
              text={" React router"}
              currentCode={currentCode}
              history={history}
            />
            <Item
              code ={"art20200616004"}
              text={" React SSR server"}
              currentCode={currentCode}
              history={history}
            />
            <Item
              code ={"art20200616005"}
              text={""}
              currentCode={currentCode}
              history={history}
            />
          </ul>
        </div>
      </CSSTransition>
      <div className="navCtrl" onClick={handleShowChange}>
        <XBurger isActive={isShow} />
      </div>
    </Fragment>
  );
}

export default NavBar;
